import { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useSelector, useDispatch } from 'react-redux';

import { history } from '_helpers';
import { faturaActions, alertActions } from '_store';
import { clienteActions } from '_store';
import { produtoActions } from '_store';
import { detalheFaturasActions } from '_store';


export { AddEdit };

function AddEdit() {
    const { id } = useParams();
    const [title, setTitle] = useState();
    const dispatch = useDispatch();
    const fatura = useSelector(x => x.faturas?.item);
    const [selectedClienteId, setSelectedClienteId] = useState('');
    const [selectedProdutoId, setSelectedProdutoId] = useState('');
    const clientes = useSelector(x => x.clientes.list);
    const produtos = useSelector(x => x.produtos.list);
    const faturas = useSelector(x => x.faturas.list);

    useEffect(() => {
        dispatch(clienteActions.getAll())
    }, [dispatch]);
    useEffect(() => {
        dispatch(produtoActions.getAll())
    }, [dispatch]);
    useEffect(() => {
        dispatch(faturaActions.getAll())
    }, [dispatch]);

    function getNextId(){
        var valor = 0;
        faturas?.value?.forEach(item => {
            valor = item.id > valor ? item.id : valor;
        });

        return valor;
    }


    // form validation rules 
    const validationSchema = Yup.object().shape({
        clienteId: Yup.number()
            .required('Client ID is required'),
        descricao: Yup.string()
            .required('A description is required'),
    });
    const formOptions = { resolver: yupResolver(validationSchema) };

    // get functions to build form with useForm() hook
    const { register, handleSubmit, reset, formState } = useForm(formOptions);
    const { errors, isSubmitting } = formState;

    useEffect(() => {
        if (id) {
            setTitle('Edit fatura');
            // fetch user details into redux state and 
            // populate form fields with reset()
            dispatch(faturaActions.getById(id)).unwrap()
                .then(fatura => reset(fatura));
                dispatch(clienteActions.getAll());
        } else {
            setTitle('Add Fatura');
        }
    }, []);

    async function onSubmit(data) {
        dispatch(alertActions.clear());
        const fatData = {clienteId:data.clienteId,descricao:data.descricao}
        const detData = {produtoId:data.produtoId,quantidade:data.quantidade,faturaId:getNextId()}
        console.log(fatData);
        try {
            // create or update user based on id param
            let message;
            if (id) {
                await dispatch(faturaActions.update({ id, fatData })).unwrap();
                message = 'Fatura updated';
            } else {
                await dispatch(faturaActions.register(fatData)).unwrap();
                await dispatch(detalheFaturasActions.registerDetail(detData)).unwrap();
                message = 'Fatura added';
            }

            // redirect to user list with success message
            history.navigate('/faturas');
            dispatch(alertActions.success({ message, showAfterRedirect: true }));
        } catch (error) {
            dispatch(alertActions.error(error));
        }
    }

    return (
        <>
            <h1>{title}</h1>
            {!(fatura?.loading || fatura?.error) &&
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="row">
                        <div className="mb-3 col">
                            <label className="form-label">ClienteId</label>
                            <select className="react-native-dropdown-picker" value={selectedClienteId} {...register('clienteId')} onChange={e => setSelectedClienteId(e.target.value)}>
                                <option value="">Selecione uma opção</option>
                                {clientes?.value?.map((cliente) => (
                                    <option key={cliente.id} value={cliente.id}>
                                        {cliente.nome}
                                    </option>
                                ))}
                            </select>                                
                            <div className="invalid-feedback">{errors.clienteId?.message}</div>
                        </div>
                        <div className="mb-3 col">
                            <label className="form-label">Descrição da Fatura</label>
                            <input name="descricao" type="text" {...register('descricao')} className={`form-control ${errors.descricao ? 'is-invalid' : ''}`} />
                            <div className="invalid-feedback">{errors.descricao?.message}</div>
                        </div>
                    </div>
                    <div className="row">
                    <div className="mb-3 col">
                            <label className="form-label">Produto</label>
                            <select className="react-native-dropdown-picker" value={selectedProdutoId} {...register('produtoId')} onChange={e => setSelectedProdutoId(e.target.value)}>
                                <option value="">Selecione uma opção</option>
                                {produtos?.value?.map((produto) => (
                                    <option key={produto.id} value={produto.id}>
                                        {produto.nome}
                                    </option>
                                ))}
                            </select>                                
                            <div className="invalid-feedback">{errors.clienteId?.message}</div>
                        </div>
                        <div className="mb-3 col">
                            <label className="form-label">Quantidade</label>
                            <input name="quantidade" type="text" {...register('quantidade')} className={`form-control ${errors.quantidade ? 'is-invalid' : ''}`} />
                            <div className="invalid-feedback">{errors.descricao?.message}</div>
                        </div>
                    </div>
                    <div className="mb-3">
                        <button type="submit" disabled={isSubmitting} className="btn btn-primary me-2">
                            {isSubmitting && <span className="spinner-border spinner-border-sm me-1"></span>}
                            Save
                        </button>
                        <button onClick={() => reset()} type="button" disabled={isSubmitting} className="btn btn-secondary">Reset</button>
                        <Link to="/faturas" className="btn btn-link">Cancel</Link>
                    </div>
                </form>
            }
            {fatura?.loading &&
                <div className="text-center m-5">
                    <span className="spinner-border spinner-border-lg align-center"></span>
                </div>
            }
            {fatura?.error &&
                <div class="text-center m-5">
                    <div class="text-danger">Error loading user: {fatura.error}</div>
                </div>
            }
        </>
    );
}
